<template>
    <div class="answer">
         <answer-pc :id="id"  @onSuccess="onSuccessFn"></answer-pc>
         <!-- 已答弹框提示 -->
        <el-dialog title="问卷详情" :visible.sync="userDialogFormVisible" :before-close="handleDialogClose" class="accomplish-dialog" :close-on-click-modal='false'>
            <div class="accomplish">
                <img src="@/assets/img/haveafinish.png"/>
                <p>已完成问卷填写，可预览当前问卷</p>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button  @click="handleDialogClose">取 消</el-button>
                <el-button type="primary" @click="create">预 览</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import AnswerPc from '../components/AnswerPc'

export default {
    name: 'Answer',
    components: {AnswerPc},
    data(){
        return{
           id:this.$route.query.id || '',
           userId:this.$route.query.userId || '',
           userDialogFormVisible:false,
        }
    },
    methods:{
     onSuccessFn(){
         this.userDialogFormVisible=true
     },
     create(){            
       this.$router.push({
            path:'/Iaminvolved/result',
            query:{
                formId:this.id,
                userId:this.userId,
                origin:'answer'
                }
            })
     },
     handleDialogClose(){
        //this.userDialogFormVisible = false;    
        window.history.back(-1)
     }
    }
}
</script>
<style lang="scss">
    .answer{
        background-color: #fff;
         width:100%;
        height: 100%;
    }
    .accomplish-dialog{
    .el-dialog__body{
        height: 230px;
        position:relative;
        .accomplish{
            width:387px;
            height: 230px;
            position:absolute;
            left:0;
            top:0;
            right:0;
            bottom:0;
            margin:auto;
            img{
                width:100%;
                height:160px;  
            }
            p{
                width:100%;
                height:18px;
                font-size:18px;
                font-family:PingFang SC;
                font-weight:500;
                color:#03B187;
                margin-top:40px;
                text-indent: 60px;
            
            }
        }
    }
    
}
</style>


